import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import '../assets/css/plugins/lineicons.css';
import '../assets/css/bootstrap.min.css';

class ModalComponent extends Component {
    render() {
        return (
            <div className="modal fade" id="myModal" role="dialog">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal">&times;</button>
                            <h4 className="modal-title">{this.props.data.title}</h4>
                        </div>
                        <div className="modal-body">
                            <p>
                                {/* <img src={this.props.data.user.avatar_url} style={{width:'100px',float:'left',padding:'10px'}} alt=""/> */}
                                {this.props.data.body}
                            </p>
                        </div>
                        <div className="modal-footer">
                            <button type="button" style={{color:'#999'}} className="btn btn-default" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ModalComponent;


