import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import '../assets/css/plugins/lineicons.css';
import '../assets/css/bootstrap.min.css';
import ModalComponent from './modal';

class ListComponent extends Component {
    constructor() {
        super();
        this.state = {
            data: [],
            modal: {}
        };
        this.openModal = this.openModal.bind(this);
    }
    componentDidMount() {
        fetch('https://api.github.com/repos/facebook/react/issues')
            .then(response => response.json())
            .then(json => {
                console.log(json);
                this.setState({
                    data: json
                });
            });
    }
    openModal(data) {
        this.setState({
            modal: data
        });
    }
    render() {
        var canvases = this.state.data.map((data) => {
            return <div key={data.id} className="row benefit-notes">
                <div className="col-md-2">
                    <img src={data.user.avatar_url} style={{ width: '50px' }} />
                </div>
                <div className="col-md-8">
                    <h4>{data.title}</h4>
                </div>
                <div className="">
                    <button type="button" onClick={this.openModal.bind(this, data)} className="btn btn-info btn-lg" data-toggle="modal" data-target="#myModal">View Detail</button>
                </div>
            </div>
        });
        return (
            <div>
                <section className="row the-benefits" id="features" style={{ borderBottom: '1px solid #ccc' }}>
                    <div className="container">
                        <div className="row section-header wow fadeInUp">
                            <h2>React Issues</h2>
                        </div>
                        {canvases}
                    </div>
                </section>
                <ModalComponent data={this.state.modal} />
            </div>
        );
    }
}

export default ListComponent;




